import React from 'react';
import {  PanelHeader,  Avatar, Div, HeaderButton, List, Cell, Snackbar, Group, InfoRow, Progress, Tabs, TabsItem} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
var axios = require('axios');



class Questions extends React.Component {
constructor(props){
super(props)

this.state ={
activeTab:'questions'

}
}




	render=(e)=>{
		return(
			<Div>
			     <Tabs theme="light">
                <TabsItem
                 onClick={() => this.setState({ activeTab: 'questions' })}
                  selected={this.state.activeTab === 'questions'}
                >
                  Задания
                </TabsItem>
                <TabsItem
                     onClick={() => this.setState({ activeTab: 'rr' })}
                  selected={this.state.activeTab === 'rr'}

                >
                  Рейтинг
                </TabsItem>
          </Tabs>

          {this.state.activeTab === "questions" ?    <List>
              {this.props.quest.map((name,i)=>
                <Cell key={i}
                      before={<Avatar src={name.img} size={72} />}
                      size="l">{"  "+name.name+" "}</Cell>
              )}
              </List>:<List>
              {this.props.rr.map((name,i)=>
                <Cell key={i}
                      before={<Avatar src={name.img} size={72} />}
                      size="l">{"  "+name.first_name+" "+name.last_name+"  "+name.ball}</Cell>
              )}
              </List>}

              </Div>
			);
	}
}
export default Questions;
